/* MyWork CSS Template */

#templateWrapper{
    width: 100%;
    height: 100%;
    border-radius: 5px;
    overflow: hidden;
    display: flex;
    flex-direction: row;
}

.innerBox{
    display: flex;
    flex-direction: column;
}

#right{
    flex-basis: 35%;
    position: relative;
    /*justify-content: center;*/
    /*align-items: center;*/
    /*overflow: hidden;*/
}

#rightWorkCont{
    flex-basis: 85%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    position: relative;
}

img{
    width: 100%;
    cursor: pointer;
    /*-webkit-box-shadow: 0px 0px 42px -9px rgba(74,74,74,1);*/
    /*-moz-box-shadow: 0px 0px 42px -9px rgba(74,74,74,1);*/
    /*box-shadow: 0px 0px 42px -9px rgba(74,74,74,1);*/
}

#middle{
    flex-basis: 36%;
    position: relative;
}

.middleInnerBox{
    position: relative;
    flex-basis: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}

#left{
    flex-basis: 29%;
    position: relative;
}

.workTemp{
    width: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}