/*Main Compoent CSS Template*/
#marco {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 99vw;
    height: 98.5vh;
    background: #f2f2f2;
    border-radius: 5px;
}
#marco::after {
    position: absolute;
    content: "";
    z-index: -5;
    width: calc(100vw);
    height: calc(100vh);
    background: linear-gradient(60deg, #5f86f2, #a65ff2, #f25fd0, #f25f61, #f2cb5f, #abf25f, #5ff281, #5ff2f0);
    background-size: 300% 300%;
    background-position: 0 50%;
    animation: moveGradient 7.5s alternate infinite;
}

.codeDream{
    background-image: url(https://media.giphy.com/media/2tTiCSfEEP5QS5TjGr/source.gif);
    background-repeat: repeat-x;
    color: rgba(255,255,255,0.18);
    -moz-background-clip: text;
    -webkit-background-clip: text;
    text-transform: uppercase;
    font-size: 8vw;
    font-weight: 500;
    font-family: 'Raleway', sans-serif;
    letter-spacing: 3px;
}

#meNameWrapper{
    display: flex;
    align-content: center;
    align-items: center;
    flex-direction: column;
}

.wrappersWrapper{
    width: 100%;
    height: 100%;
    background: black;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    border-radius: 5px;
}

#loadText{
    margin: 0;
    font-size: 5vw;
    font-weight: 600;
}

@keyframes moveGradient {
    50% {
        background-position: 100% 50%;
    }
}
